// javascript
function arrayAdd(str1,str2){
    var str1length = str1.length;
    var str2length = str2.length;
    var tmp = "";
    if(str1length != str2length){
        for(var i=0;i<Math.abs(str1length - str2length);i++){
            tmp = tmp + "0";
        }
    }//
    var length = 0;
    if(str1length == str2length){
        length = str1length;
    }else{
        if (str1length < str2length){
            str1 = tmp + str1; length = str2length;
        }else{
            str2 = tmp + str2; length = str1length;
        }
    }//0

    var array1 = str1.split("");    //
    var array2 = str2.split("");	//
    var array3 = new Array(length + 1);    //1

    var add = 0;	//
    for(var i=length-1;i>=0;i--){  //
        var x1 = array1[i];
        var x2 = array2[i];
        var addvalue = parseInt(x1) + parseInt(x2) + parseInt(add);
		if(addvalue >= 10){//
            if(addvalue >= 20){	//
                array3[i+1] = addvalue - 20;
                add = 2;
            }else{		//1array3
                array3[i+1] = addvalue - 10;
                add = 1;
            }
        }else{	// array3
            array3[i+1] = addvalue;
            add = 0; //0
        }
    }

    if(add > 0){//0array3[0]
        array3[0] = add; //0array3[0]N/A0java
    }
    return array3.join("");//

}
export const encrypt = (str,pwd) => {
    if(pwd == null || pwd.length <= 0) {
        alert("Please enter a password with which to encrypt the message.");
        return null;
    }
    var prand = "";
    for(var i=0; i<pwd.length; i++) {
        prand += pwd.charCodeAt(i).toString();
    }

    var sPos = Math.floor(prand.length / 5);
    var mult = parseInt(prand.charAt(sPos) + prand.charAt(sPos*2) + prand.charAt(sPos*3) + prand.charAt(sPos*4) + prand.charAt(sPos*5));
    var incr = Math.ceil(pwd.length / 2);
    var modu = Math.pow(2, 31) - 1;
    if(mult < 2) {
        alert("Algorithm cannot find a suitable hash. Please choose a different password. \nPossible considerations are to choose a more complex or longer password.");
        return null;
    }

    var salt = Math.round(Math.random() * 1000000000) % 100000000;
    prand += salt;
    while(prand.length > 10) {
        var x1 = prand.substring(0, 10);
        var x2 = prand.substring(10, prand.length);
        prand = arrayAdd(x1 , x2);
    }
    prand = (mult * prand + incr) % modu;

    var enc_chr = "";
    var enc_str = "";
    for(var i=0; i<str.length; i++) {
        enc_chr = parseInt(str.charCodeAt(i) ^ Math.floor((prand / modu) * 255));
        if(enc_chr < 16) {
            enc_str += "0" + enc_chr.toString(16);
        } else
            enc_str += enc_chr.toString(16);
        prand = (mult * prand + incr) % modu;
    }

    salt = salt.toString(16);
    while(salt.length < 8)
        salt = "0" + salt;
    enc_str += salt;

    return enc_str;
}




