<template>
    <!---->

    <div class="app-wrap">
        <dl class="form-list mt-35">
            <dd>
                <div class="title"></div>
                <div class="input-box">
                    <input ref="oldPassword" v-once v-model="pwdFormData.oldPassword" type="password" placeholder="">
                    <div class="clear-input">
                        <svg class="icon" aria-hidden="true" @click="$refs.oldPassword.value=''">
                            <use xlink:href="#icon-guanbi"></use>
                        </svg>
                    </div>
                </div>
            </dd>
            <dd>
                <div class="title"></div>
                <div class="input-box">
                    <input ref="newPassword" v-once v-model="pwdFormData.newPassword" type="password" placeholder="6">
                </div>
            </dd>
            <dd>
                <div class="title"></div>
                <div class="input-box">
                    <input v-model="pwdFormData.againPwd" type="password" placeholder="">
                </div>
            </dd>
        </dl>
        <div class="btn-box mt-35">
            <a class="btn btn-blue" @click="editPwd" href="javascript:void(0)"></a>
        </div>

        <!---->
        <wxshare/>
    </div>
</template>

<script>

    import {mapGetters} from 'vuex';
    import util from '@/util/util';
    import {encrypt} from '@/util/encryptPassword';
    import { Toast,MessageBox } from 'mint-ui';
    import wxshare from '../../../components/wxshare'
    export default {
        name: "userEdit",
        data(){
            return {
                pwdFormData:{
                    oldPassword:'',//
                    newPassword:'',//
                    againPwd:''//
                }
            }
        },
        methods:{
            editPwd(){
                let rules = {
                    oldPassword:[{isNull:true,message:'!'}],
                    newPassword:[{isNull:true,message:'!'}],
                    againPwd:[{isNull:true,message:'!'}]
                };
                let rs = util.validateForm(this.pwdFormData,rules);
                if(rs.hasNull){
                    Toast({message:rs.info, duration:3000});
                    return;
                }
                if(this.pwdFormData.newPassword!=this.pwdFormData.againPwd){
                    Toast({
                        message:'!',
                        duration:3000
                        //className:'mint-toast-icon mintui mintui-success'
                    });
                    return;
                }
                //

                this.pwdFormData.oldPassword = encrypt(this.$refs.oldPassword.value,this.key);
                this.pwdFormData.newPassword = encrypt(this.$refs.newPassword.value,this.key);
                this.$store.dispatch('modifyPwd',this.pwdFormData).then(resp=>{
                    console.log(resp.data);
                    if(resp.data){
                        MessageBox.alert('!','').then(action => {
                            this.$router.push({path:'/register'});
                        });
                    }
                })
            }
        },
        created(){
            this.$store.dispatch('getKey',{});
        },
        computed:{
            ...mapGetters({
                key:'getKey'
            })
        },
        components:{
            wxshare
        }
    }
</script>

<style scoped>
</style>



