<template>
<div>
    <div class="activity-rules-title"><span></span></div>
    <ul class="activity-rules-list">
      <li><b>{{enterpriseName}}</b></li>
      <li><b>{{userName}}({{idCardNo|idCardMark}})</b></li>
      <li></li>
      <li><b>{{prod_money_inc}}</b>: <b>{{prod_money_inc|DX}}</b></li>
      <li></li>
      <li></li>
      <li>  <b>{{prodTerm}}</b><span v-if="fixedTerm==2" ></span> <span v-if="fixedTerm==1" ></span>    <b>{{prodRate}}</b> </li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li><b>{{enterpriseName}}</b></li>
      <li><b>{{userName}}({{idCardNo|idCardMark}})</b></li>
      <li>&nbsp;</li>
      <li><b>{{orderDate|transDate}}</b></li>
    </ul>
</div>
</template>
<script>
    import util from '@/util/util'
    import { Toast, MessageBox } from 'mint-ui'
    export default {
        name: "agreement",
        data(){
            return {
              enterpriseName:"",
              userName:"",
              idCardNo:"",
              fixedTerm:"",
              prod_money_inc:"",
              prodRate:"",
              prodTerm:"",
              orderDate:""
            }
        },
    filters:{
            DX(n) {
            if (!/^(0|[1-9]\d*)(\.\d+)?$/.test(n))
                return "";
            var unit = "", str = "";
                n += "00";
            var p = n.indexOf('.');
            if (p >= 0)
                n = n.substring(0, p) + n.substr(p+1, 2);
                unit = unit.substr(unit.length - n.length);
            for (var i=0; i < n.length; i++)
                str += ''.charAt(n.charAt(i)) + unit.charAt(i);
            return str.replace(/(|||)/g, "").replace(/()+/g, "").replace(/(||)/g, "$1").replace(/()|()/g, "$1$2").replace(/^?|/g, "").replace(/$/g, "");
            },
            transDate(time){
                return time.substring(0,4)+""+time.substring(5,7)+""+time.substring(8,10)+"";
            },
            idCardMark(idCardNo){
                return idCardNo.substring(0,2)+"**************"+idCardNo.substring(16,18);
            }
        },
        methods:{},
        created(){
            
            this.fixedTerm = this.$route.params.fixedTerm;
            this.prod_money_inc = this.$route.params.prod_money_inc;
            this.prodRate = this.$route.params.prodRate;
            this.prodTerm = this.$route.params.prodTerm;
            this.orderDate = this.$route.params.orderDate;
            // fastClick.attach(document.body);
            util.axiosPost('/eebpc/wealthCenter/getEnterpriseAndEmployeeName').then(resp=>{
                console.log(resp.data);
                this.enterpriseName = resp.data.enterpriseName;
                this.userName = resp.data.userName;
                this.idCardNo = resp.data.idCardNo;
            });
        },
        mounted(){

        },
        computed:{
        
        },
        components:{
            
        }
    }
</script>

<style scoped>
</style>



