<template>
    <div class="app-wrap">
        <div class="link-info-box">
            <dl class="link-info-list">
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info">{{v.prodName}}</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info">{{v.prodTerm}}</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info">{{v.prodRate}}</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title">
                            <span></span>
                            <span class="ts"></span>
                        </div>
                        
                        <div class="info">{{dialogPopUpInfo.currentProdRate}}</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info"></div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info"></div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info">**/12</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info"></div>
                    </a>
                </dd>
                <dt>
                      
                </dt>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info"> {{v.prod_money_inc}}</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info">{{v.orderDate}}</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info">{{v.interestsDate}}</div>
                    </a>
                </dd>
                <dd>
                    <a href="javascript:void(0)">
                        <div class="title"></div>
                        <div class="info"> {{v.preliminary_earnings}}</div>
                    </a>
                </dd>
                <dt></dt>
                <dd>
                    <a href="javascript:void(0)" @click="showAgreement">
                        <div class="title"></div>
                        <div class="info">
                            
                            <svg class="icon" aria-hidden="true">
                                <use xlink:href="#icon-jiantou"></use>
                            </svg>
                        </div>
                    </a>
                </dd>
            </dl>
            <div class="bottom-blue-btn">
                <a @click="show" href="javascript:void(0)"></a>
            </div>
        </div>
        <div v-show="dialogPopUp" class="dialog-pop-up">
            <div class="dialog-content">
                <div class="title"></div>
                <div class="info">
                     {{dialogPopUpInfo.prod_money_inc}} -  {{dialogPopUpInfo.prodTerm}}
                    <div class="center">
                        {{dialogPopUpInfo.currentProdRate}}<br>
                         {{dialogPopUpInfo.starData}}<br>
                         {{dialogPopUpInfo.endData}}
                    </div>
                </div>
                <div class="footer">
                    <div class="l">
                        <a @click="transferCurrentCancel" href="javascript:void(0)"></a>
                    </div>
                    <div class="r">
                        <a @click="transferCurrentSure(dialogPopUpInfo.prodId, dialogPopUpInfo.prodOrderId, dialogPopUpInfo.prod_money_inc)" class="blue" href="javascript:void(0)"></a>
                    </div>
                </div>
            </div>
            <div class="dialog-bg"></div>
        </div>
        <div v-show="withdrawalPopUp" class="withdrawal-pop-up">
            <div class="withdrawal-content pb-100">
                <div class="head">
                    <router-link class="i" :to="{ path: '/' }">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-jiantou"></use>
                        </svg>
                    </router-link>
                    
                    <router-link class="qd" :to="{ path: '/' }"></router-link>
                </div>
                <div class="pic mt-10"><img src="@/assets/img/pic.png"></div>
                <div class="mt-20 center fs-16">
                    
                </div>
                <div class="mt-15" style="line-height: 150%;">
                     <router-link class="blue" :to="{ path: '/' }"></router-link>  <router-link class="blue" :to="{ path: 'transactionDetail' }"></router-link> 
                </div>
            </div>
            <div class="withdrawal-bg"></div>
        </div>
        <!--<div class="dialog-pop-up">    
            <div class="dialog-content">
                <div class="title"></div>
                <div class="info">
                    
                </div>
                <div class="footer">
                    <div class="l">
                        <a href="javascript:void(0)"></a>
                    </div>
                    <div class="r">
                        <a class="blue" href="javascript:void(0)"></a>
                    </div>
                </div>
            </div>   
            <div class="dialog-bg"></div>
        </div>-->
    </div>
    </template>

<script>
    import $ from 'jquery'
    import AIP from '@/util/aip'
    import util from '@/util/util'
    import { Toast, MessageBox } from 'mint-ui'
    import {mapGetters} from 'vuex';
    import {encrypt} from '@/util/encryptPassword'
    import wxshare from '../../../components/wxshare'
    export default {
        name: "investRegularly",
        data(){
            return {
                v:"",
                dialogPopUp: false,
                withdrawalPopUp: false,
                dialogPopUpInfo: '',
                submitData: {}
            }
        },
        methods:{
            show(){
                this.dialogPopUp = true;
            },
            showAgreement(){
                    this.$router.push({name:"agreement1", params:{fixedTerm:this.v.fixedTerm,prod_money_inc:this.v.prod_money_inc,prodRate:this.v.prodRate,prodTerm:this.v.prodTerm,orderDate:this.v.orderDate}})
            },
            transferCurrent(orderId, money, term){
                util.axiosPost('/eebpc/buy/unsubscribe', {
                    prodOrderId: orderId
                })
                .then(res => {
                    console.log(4)
                    console.log(res)
                    this.dialogPopUpInfo = res.data
                    this.dialogPopUpInfo.prod_money_inc = money
                    this.dialogPopUpInfo.prodTerm = term

                })
                .catch( err => {
                    console.log(5)
                    console.log(err)
                })
            },
            transferCurrentCancel(){
                this.dialogPopUp = false
            },
            transferCurrentSure(prodId, prodOrderId, money){
                this.submitData.prodId = prodId
                this.submitData.prodOrderId = prodOrderId
                this.submitData.prodMoney = money
                this.dialogPopUp = false
                AIP.addKeyboardPayPassword('', {callback:this.getV, forgot:this.getF})
            },
            getV(){
                this.submitData.password = encrypt($("#passWordInput").val(),this.key);
                util.axiosPost('/eebpc/buy/unsubscribeSubimt',
                    this.submitData
                )
                .then( response => {
                    console.log(this.submitData);
                    console.log(response);
                    console.log('1');

                   if(response.data.state == '0'){
                       this.withdrawalPopUp = true;
                       setTimeout(() => {
                           this.$router.push({path:'/'});
                       },2000)
                   }
                })
                .catch( err => {
                    console.log('--catch--')
                    console.log(err);
                });
            },
            getF(){
                 this.$router.push({path:'/userValidate'});
            }
        },
        created(){
            this.$store.dispatch('getKey',{});
            this.v = this.$route.params.v || "";
            if(this.v==''||this.v==null){
             this.v = JSON.parse(sessionStorage.getItem("investmengtDetail_v"));
            }else{
                sessionStorage.setItem("investmengtDetail_v",JSON.stringify(this.v));
            }
            this.transferCurrent(this.v.prod_order_id, this.v.prod_money_inc, this.v.prodTerm);
            
        },
        mounted(){

        },
        computed:{
            ...mapGetters({
                key:'getKey'
            })
        },
        components:{
            wxshare
        }
    }
</script>

<style scoped>
</style>



