<template>
    <!---->
    <div class="app-wrap">
            <div style="overflow:scroll; height: 40rem; -webkit-overflow-scrolling : touch;">
                <mt-loadmore bottomPullText="" :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">
                    <dl class="freeze-detail">
                        <dd v-for="(v,i) in totalAllList" :key="i">
                            <div class="date"><span></span> <span class="gray">{{v.mount| fixedAmount(2)}}</span></div>
                            <div class="date mt-10"><span></span> <span class="gray"></span></div>
                            <div class="num orange" v-if="Number(v.reviewState)==1">
                                <span class="gray">{{v.creatTime | formatDate('MM-dd hh:mm')}}</span>
                            </div>
                            <div class="num" v-else-if="Number(v.reviewState)==2">
                                <span class="gray">{{v.creatTime | formatDate('MM-dd hh:mm')}}</span>
                            </div>
                            <div class="num orange" v-else-if="Number(v.reviewState)==3">
                                <span class="gray">{{v.creatTime | formatDate('MM-dd hh:mm')}}</span>
                            </div>
                            <div class="num orange" v-else-if="Number(v.reviewState)==4"> {{v.stateInfo}}
                                <span class="gray">{{v.creatTime | formatDate('MM-dd hh:mm')}}</span>
                            </div>
                        </dd>
                    </dl>
                </mt-loadmore>
            </div>

        <!---->
        <wxshare/>
    </div>
</template>

<script>

    import util from '@/util/util';
    import { Toast,Loadmore,Indicator } from 'mint-ui';
    import wxshare from '../../../components/wxshare'
    export default {
        name: "freezeDetail",
        filters:{
            formatDate(val,pattern){
                return util.formatDate.format(val,pattern);
            },
            fixedAmount(val,pattern){
                return util.isNumber(val)?Number(val).toFixed(pattern):'0.00';
            }
        },
        data(){
            return {
                pageData:{
                    currentPage:1,    //currentPage  
                },
                //
                resultData:{
                    endRow:0,
                    nextPage:0,//
                    pageIndex:0,//
                    pageSize:10,//
                    prePage:1,//
                    record:0,//
                    sartRow:0,
                    totalPageCount:0,//
                    houseList:[]
                },
                totalAllList:[],
                allLoaded:false,//
            }
        },
        methods:{
            loadTop(){
                // 
                console.log('');
                this.changeType();
                this.$refs.loadmore.onTopLoaded();
            },
            loadBottom() {
                // 
                console.log('');

                if(this.resultData.pageIndex==this.resultData.totalPageCount){//
                    this.allLoaded = true;// 
                    Toast({message:'!',duration:3000});
                }
                this.pageData.currentPage++;
                this.commContent();
                this.$refs.loadmore.onBottomLoaded();
            },
            //
            changeType(){
                //
                //
                this.pageData.currentPage = 1;
                this.totalAllList = new Array();//
                this.allLoaded = false;// 
                this.commContent();
            },
            commContent(){
                Indicator.open();//
                this.$store.dispatch('queryFreezeDetail',this.pageData).then(resp=>{
                    console.log(resp.data);
                    this.resultData = resp.data;
                    this.totalAllList = this.totalAllList.concat(this.resultData.houseList);

                    Indicator.close();
                     if(resp.data.record=='0'){
                    Toast({message:'!',duration:3000});
                }
                });
            }
        },
        created(){
            this.commContent();
        },
        components:{
            wxshare
        }
    }
</script>

<style scoped>
</style>



